<template>
  <div class='filter-list'>
    <div class="filter-type" ref="filterType" v-for="(item, index) in filterData[type]" :key="index">
      <div class="filter-item-list" :class="{active: index1 === activeArr[index]}" @click="setFilter(index, index1)" v-for="(filter, index1) in item" :key="index1">{{filter}}</div>
    </div>
  </div>
</template>
<script>
export default {
  props: {
    type: {
      type: Number,
      default: 1              // 1 2 3 4
    },
    filterType: {
      type: String
    }
  },
  data () {
    return {
      filterList: [],
      filterData: {                       // 1, 2 3  4
        1: {
          type: ['', '', '', '', '', '', '', '', '', '', '', ''],
          area: ['', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', ''],
          year: ['', '2019', '2018', '2017', '2016', '2015', '2011-2014', '2000-2010', '90', '80', '']
        },
        2: {
          type: ['', '', '', '', ''],
          area: ['', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', ''],
          year: ['', '2019', '2018', '2017', '2016', '2015', '2011-2014', '2000-2010', '90', '80', '']
        },
        3: {
          area: ['', '', '', '', '', ''],
          year: ['', '2019', '2018', '2017', '2016', '2015', '2011-2014', '2000-2010', '90', '80', '']
        },
        4: {
          area: ['', '', '', '', '', ''],
          year: ['', '2019', '2018', '2017', '2016', '2015', '2011-2014', '2000-2010', '90', '80', '']
        }
      },
      activeArr: {
        type: 0,
        area: 0,
        year: 0
      }
    }
  },
  methods: {
    getFilter () {
     
    },
    setFilter (index, index1) {
      this.activeArr[index] = index1
      this.$set(this.activeArr, index, index1)
      let data = {}
      this.type = this.type * 1
      if (this.type === 1 || this.type === 2) {
        data = {
          type: this.filterData[this.type].type[this.activeArr.type],
          area: this.filterData[this.type].area[this.activeArr.area],
          year: this.filterData[this.type].year[this.activeArr.year]
        }
      } else {
        data = {
          area: this.filterData[this.type].area[this.activeArr.area],
          year: this.filterData[this.type].year[this.activeArr.year]
        }
      }
      this.$emit('getFilter', data)
    }
  }
}
</script>
<style lang="scss" scoped>
.filter-list {
  padding-top: 15px;
}
.filter-type {
  white-space: nowrap;
  overflow-y: hidden;
  padding: 15px 30px;
  .filter-item-list {
    display: inline-block;
    padding: 0 30px;
    line-height: 54px;
    white-space: nowrap;
    font-size: 30px;
    &.active {
      background: #f4a82b;
      border-radius: 27px;
      color: #FFFFFF;
    }
    // min-width: 0;
  }
}
</style>



